<template>
    <div id="qdfss">
      <div  class="qdf_info">
        <header  class="qdf_header">
          <span >
            <img
              @click="qdf_fh()"
              src="data:image/png;base64,"
              alt
            />
          </span>
          <span >
            <input  placeholder="请输入内容" class="input" v-model="ssnr"/>
          </span>
          <span v-if="this.ssnr == ''"  @click="qdf_fh()">取消</span>
          <span v-else @click="ss1" style="color:#000">搜索</span>
        </header>
        <section v-if="this.fby_list.length ==0" class="qdf_search">
          <header  class="qdf_his-header">
            <p >
              <span >历史搜索</span>
              <span>
                <i><van-icon name="delete" @click="shanchu"/></i>
              </span>
            </p>
          </header>
          <div class="list" >
              <ul>
                <li @click="fby_ls(item)" v-for="(item,index) of lsjl" :key="index">
                    {{ item }}
                </li>
              </ul>
          </div>
        </section>
        <ul class="fby_sc" v-else >
        <li v-for="(item,index) of fby_list" :key="index">
          <div>
            <p>
              {{ item.title }}
            </p>
          </div>
            <p>共{{ item.section_num }}课时</p>
            <p v-for="(c,index) of item.teachers_list" :key="index">
              <img :src="c.teacher_avatar" alt="">
              {{ c.teacher_name }}
            </p>
            <p><span>{{ item.sales_num }}已报名</span>
            <span>{{ item.underlined_price | jg}}</span></p>
        </li>
      </ul>
      </div>
    </div>
 
</template>

<script>
export default {
  data() {
    return {
      ssnr:'',
      lsjl:[],
      fby_list:[]
    };
  },
  filters:{
    jg(val){
      if(val == 0){
        return "免费"
      }else{
        return (val/100).toFixed(2)
      }
    },
  },
  created() {
    // 历史记录
     let his =JSON.parse(window.localStorage.getItem("lsju"));
          if(his){
            this.lsjl = his  
          }
  },
  methods: {
      qdf_fh(){
          this.$router.go(-1)
      },
     async ss1(){
        let { data:res }  = await this.$http.get(`/api/app/courseBasis?limit=10&page=1&course_type=0&keywords=${this.ssnr}`)
          console.log(res)
          this.fby_list = res.data.list
          console.log(this.fby_list )
          // 历史记录
          // 创建一个空数组
          let arr = []
          ////如果本地储存有数据，获取值
          let his =JSON.parse(window.localStorage.getItem("lsju"));
          if(his){
            arr = his  
          }
          //添加绑定input框的数据
          arr.push(this.ssnr)
          // console.log(arr)
          //储存历史记录
          localStorage.setItem("lsju",JSON.stringify(arr))
          this.lsjl = his
          // console.log(this.lsjl)
          this.ssnr = ""
      },
      //删除历史记录
      shanchu(){
           this.lsjl = []
           window.localStorage.removeItem("lsju")
      },
      fby_ls(v){
         this.ssnr = v
      }

  },
  mounted() {}
};
</script>

<style scoped lang="scss" >
#qdfss{
   background: #f0f2f5;
  min-height: 100vh;
  box-sizing: border-box;
}
.qdf_info {
    height: 500px;
}
.qdf_header {
    height: 1.25333rem;
    height: 12.53333vw;
    border-bottom: 1px solid #eaeaea;
    background: #fff;
}
.qdf_header span:first-child {
    flex: 1;
    margin-right:0.2rem;
}
.qdf_header span:first-child img {
    width: .24rem;
    width: 2.4vw;
    height: .42667rem;
    height: 4.26667vw;
    padding-left: .4rem;
    padding-left: 4vw;
}
.qdf_header span:nth-child(2) {
    flex: 6;
}
.qdf_header span:nth-child(2) input {
  
    height: .8rem;
    height: 8vw;
    padding-left: .74667rem;
    padding-left: 7.46667vw;
    border: none;
    background: #e4e7ed;
    border-radius: .4rem;
    border-radius: 4vw;
    font-size: .4rem;
    font-size: 4vw;
    width: 73%;
}
.qdf_header span:nth-child(3) {
    flex: 1;
    font-size: .37333rem;
    font-size: 3.73333vw;
    color: #d2d2d1;
    margin-left:0.3rem;
}
.qdf_search {
    margin: .4rem .45333rem;
    margin: 4vw 4.53333vw;
}
.qdf_his-header {
    height: 1.06667rem;
    height: 10.66667vw;
}
.qdf_his-header p {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.qdf_his-header p span:first-child {
    font-size: .4rem;
    font-size: 4vw;
    color: #333;
    font-weight: 700;
}
.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    ul{
      width: 100%;
      height: 200px;
      display: flex;
      flex-wrap: wrap;
      li{
        font-size: 14px;
        height: 20px;
        font-size: 16px;
        padding: 8px 14px;
        background: #ccc;
        margin-right: 10px;
        color: #fff;
        border-radius: 20px;
      }
    }
}
.qdf_placeholder {
    height: 0;
    pointer-events: none;
}
.fby_sc{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  li{
    width: 92%;
    height: 230px;
    margin-top: 10px;
    background: #fff;
    padding: 5px 5px;
    border-radius: 4px;
    div{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
          height: 60px;
          width: 88%;
          color: #000;
        }
    }
     p:nth-of-type(1){
       height: 60px;
       color: #000;
       font-size: 16px;
    }
     p:nth-of-type(2){
        height: 60px;
        margin-bottom: 2px;
        display: flex;
        align-items: center;
        color: #999;
      img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
     p:nth-of-type(3){
       border-top: 1px #ccc solid;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        span:nth-of-type(1){
          color: #999;
        }
        span:nth-of-type(2){
          color:#44a426;
        }
    }
    
  }
}
</style>
