<template>
  <div class="kc">
    <div class="header">
      <van-nav-bar class title="特色课">
        <template #right>
          <van-icon name="search" size="18" @click="go_ss()" />
        </template>
      </van-nav-bar>
      <van-dropdown-menu active-color="darkorange">
        <!-- 分类 -->
        <van-dropdown-item title="分类" class="selects" ref="ok">
          <!-- 年级 -->
          <h6>年级</h6>
          <ul class="ulItem">
            <li 
            @click='activeStudyIndex = index' 
            :style="{background:activeStudyIndex == index?'cornflowerblue':''}" 
            v-for="(item,index) in classStudy" :key="index">{{item.name}}</li>
          </ul>
          <!-- 学科 -->
          <h6>学科</h6>
          <ul class="ulItem">
            <li 
             @click='activeBanIndex = i' 
            :style="{background:activeBanIndex == i?'cornflowerblue':''}" 
            v-for="(v,i) in classBan" :key="i">
            {{v.name}}
            </li>
          </ul>
          <van-button
          @click='reset'
            color="#666"
            plain
            style="font-size:16px;width:45%;height:30px;border-radius:5px;margin-left:10px;margin-bottom:20px;"
          >重置</van-button>
          <van-button
            color="darkorange"
            @click="submit"
            style="font-size:16px;width:45%;height:30px;border-radius:5px;margin-left:10px;margin-bottom:20px;"
          >确定</van-button>
        </van-dropdown-item>
        <!-- 排序 -->
        <van-dropdown-item title="排序" ref='ok3'>
          <div class="sort">
            <p @click='Sortfn'> 综合排序</p>
             <p @click='News'> 最新</p>
              <p @click='Hots'> 最热</p>
               <p @click='Sortd'> 价格从低到高</p>
                <p @click='Sortg'> 价格从高到低</p>
          </div>
        </van-dropdown-item>
        <!-- 筛选 -->
        <van-dropdown-item title="筛选" ref='ok2'>
          <div class="Filters">
            <ul>
              <li @click="fby_free(index,item.id)" 
              :style="{background:active == index?'#eb6100':''}"
              v-for="(item,index) in filterList" :key="index">{{ item }}</li>
            </ul>
            <ul>
              <li></li>
            </ul>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <div class="kong"></div>
    <!-- 课程列表开始 -->

    <ul class="qdf_ul">
      <li class="qdf_li" @click="qdf_tzxqy(item.id)" v-for="(item,index) in  courseList" :key="index">
        <p class="qdf_title">
          <font>{{ item.title }}</font>
        </p>
        <div class="qdf_time">
          <p class="qdf_after"></p>
          <p>
            <van-icon name="clock-o" size="10" />
            {{ item.start_play_date | time}}~{{ item.end_play_date |time}}&emsp;共 {{ item.total_periods }}课时
          </p>
        </div>
        <div class="qdf_teacher">
          <div class="qdf_teacher_item" v-for="(v,i) in item.teachers_list" :key="i">
            <img :src="v.teacher_avatar" alt />
            <font>{{ v.teacher_name }}</font>
          </div>
        </div>
        <p class="qdf_info">
          <span class="qdf_person">{{ item.sales_num }}人已报名</span>
          <font class="qdf_free">{{item.price | priceShow }}</font>
        </p>
      </li>
      <div class="bottom"></div>
    </ul>
    <!-- 课程列表结束 -->
  </div>
</template>

<script>
export default {
  name: "kc",
  components: {},
  props: {},
  data() {
    return {
      active:-1,
      activeStudyIndex:-1,
      activeBanIndex:-1,
      courseInfo: {
        page: 1, //第几页开始获取
        limit: 10, //请求数据数量
        course_type:0, //筛选类型
        classify_id:"",
        order_by: "", //排序方式：（0综合排序，1最新，2最热，3价格由低到高，4价格由高到低）
        attr_val_id: '', //分类id
        is_vip: 0 //是否是vip会员  默认0
      },
      courseList:[],
      sortList: [
        {
          id: 0,
          title: "综合排序"
        },
        {
          id: 1,
          title: "最新"
        },
        {
          id: 2,
          title: "最热"
        },
        {
          id: 3,
          title: "价格从低到高"
        },
        {
          id: 4,
          title: "价格从高到低"
        }
      ],
      classStudy:[
              {
                id:1,
                name:'初一'
              },
                {
                id:2,
                name:'初二'
              },
                {
                id:3,
                name:'初三'
              },
                {
                id:4,
                name:'高一'
              },
                {
                id:5,
                name:'高二'
              }

      ],
      classBan:[
            {
                id:7,
                name:'语文'
            },
            {
                id:8,
                 name:'数学'
            },
            {
                id:9,
                 name:'英语'
            },
            {
                id:12,
                name:'物理'
            },
            {
                id:13,
                name:'化学'
            },

      ],
      filterList: [
        "全部",
        "大班课",
        "小班课",
        "公开课",
        "点播课",
        "面授课",
        "音频课",
        "系统课",
        "图文课",
        "会员课"
      ],
      loading: false,
      finished: false,
      List: [],
      Free: [],
      limit: 20,
      page: 1,
      id: "",
 
    };
  },
  filters: {
    priceShow(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    },
    time(val) {
      var moment = require("moment");
      var aaa = moment(val * 1000).format("MM月DD日 h:mm:ss");
      return aaa;
    }
  },
  watch: {},
  computed: {},
  methods: {
    go_ss() {
      this.$router.push("/qdfss");
    },
    qdf_tzxqy(id) {
      this.$router.push({
        path: "/qdfxqy",
        query: {
          id: id
        }
      });
    },
    //分类点击渲染列表
    // toSearch(){
    //   this.$http.get(`https://www.365msmk.com/api/app/courseBasis?page=1&limit=10&course_type=0&classify_id=${this.Free.child.id}&order_by=&attr_val_id=1,7&is_vip=0&`)
    // }
    // async ajaxCourseInfo() {
    //   let { data: res } = await this.$http.get(
    //     "/api/app/courseClassify"
    //   );
    //   console.log(res);
    //   if (res.code != 200) {
    //     this.$toast(res.msg);
    //     return false;
    //   }
    //   //正常的接口渲染
    //   // this.classStudy = res.data.attrclassify[0].child;
    //   // this.classBan = res.data.attrclassify[1].child;
    //   // console.log(this.classStudy, this.classBan);
    // },
    submit() { 
      //收集Id
      let arr = []
      if(this.activeStudyIndex != -1){
         let studyId = this.classStudy[this.activeStudyIndex].id
         arr.push(studyId)
      }
      if(this.activeBanIndex != -1){
         let banId = this.classBan[this.activeBanIndex].id
         arr.push(banId)
      }
       console.log(arr)
       this.courseInfo.attr_val_id = arr.join(",")
       console.log( this.courseInfo.attr_val_id)
       //更新list数据
       this.ajaxCourseList()
       //下拉消失
      this.$refs.ok.toggle();
    },
    //重置分类选项
    reset(){
        this.courseInfo.attr_val_id = ""
           //更新list数据
       this.ajaxCourseList()
         this.$refs.ok.toggle();
    },
    //综合排序
    Sortfn(){
      this.courseInfo.order_by = 0
       this.ajaxCourseList()
        this.$refs.ok3.toggle();
    },
    //最新排序
    News(){
         this.courseInfo.order_by = 1
       this.ajaxCourseList()
       this.$refs.ok3.toggle();
    },
    //最热排序
    Hots(){
       this.courseInfo.order_by = 2
       this.ajaxCourseList()
       this.$refs.ok3.toggle();
    },
    //价格从低到高排序
    Sortd(){
     this.courseList.sort((a,b)=>{
            	//排序基于的数据
              			return a.price - b.price;
                })
                this.$refs.ok3.toggle();
    },
    //价格从高到底排序
    Sortg(){
      function sortData(a, b) {
              		return b.price - a.price;
            	}	
            this.courseList.sort(sortData);
          this.$refs.ok3.toggle();
    },
    //请求特色课默认数据
    async ajaxCourseList(){
      let {data:res} = await this.$http.get('/api/app/courseBasis',{
        params:this.courseInfo
        })
      if (res.code != 200) {
        this.$toast(res.msg);
        return false;
      }
      //接口渲染
      console.log(res)
      this.courseList = res.data.list
      // console.log(this.courseList)
    },
    fby_free(i,id){
      console.log(id)
      this.active = i
      this.courseInfo.course_type = id
      console.log(this.courseInfo.course_type)
      this.ajaxCourseList()
      this.$refs.ok2.toggle()
    }
  },
  created() {},
  mounted() {
    //分类内容数据
    // this.ajaxCourseInfo();
    //默认特色课内容
    this.ajaxCourseList()
    //底部加载数据
    window.onscroll = () => {
      let scrollTop = document.documentElement.scrollTop;
      let scrollHeight = document.documentElement.scrollHeight;
      let h1 = document.documentElement.clientHeight;
      // console.log(scrollTop + h1, scrollHeight);
      if (scrollTop + h1 >= scrollHeight) {
        this.page++;
        this.$http
          .get(
            `/api/app/courseBasis?page=${this.page}&limit=${this.limit}&`
          )
          .then(res => {
            console.log(res);
            res.data.data.list.forEach(v => {
              this.courseList.push(v);
            });
          });
      }
    };
    //特色课内容
    // this.$http
    //   .get("https://365msmk.com/api/app/courseBasis?page=1&limit=20&")
    //   .then(msg => {
    //     console.log(msg);
    //     this.List = msg.data.data.list;
    //     console.log(this.List);
    //   });
    //下拉分类数据
    // this.$http.get('https://365msmk.com/api/app/courseClassify').then((res) => {
    //     console.log(res)
    //    this.Free = res.data.data.attrclassify
    //    console.log(this.Free)
    // })
  }
};
</script>
<style lang="scss"  scoped>
* {
  margin: 0;
  padding: 0;
}
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.kong {
  width: 100%;
  height: 90px;
}
.kc {
  .qdf_ul {
    padding: 0.4rem;
    padding: 4vw;
    background: #f0f2f5;
  }
  .qdf_li {
    background: #fff;
    border-radius: 0.13333rem;
    border-radius: 1.33333vw;
    padding: 0 0.37333rem;
    padding: 0 3.73333vw;
    margin-bottom: 0.4rem;
    margin-bottom: 4vw;
    position: relative;
  }
  .qdf_title {
    padding-top: 0.4rem;
    padding-top: 4vw;
  }
  .qdf_title font {
    font-size: 0.42667rem;
    font-size: 4.26667vw;
    font-family: PingFangSC-Medium;
    font-weight: 400;
    color: #333;
  }
  .qdf_time {
    display: flex;
    align-items: center;
  }
  .qdf_after {
    padding-left: 0.53333rem;
    padding-left: 5.33333vw;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACoAAAAqCAYAAADFw8lbAAAGdElEQ…7D7bIT6T8ppX5D8qmltjcNVN9Rf6pC343nMmv03YDqJvO/L6pVWKmfOusAAAAASUVORK5CYII=)
      no-repeat 0;
    background-size: 0.37333rem 0.37333rem;
    background-size: 3.73333vw 3.73333vw;
  }
  .qdf_time p {
    height: 0.8rem;
    height: 8vw;
    line-height: 0.8rem;
    line-height: 8vw;
    display: inline-block;
    font-size: 0.32rem;
    font-size: 3.2vw;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #666;
    padding-right: 0.45333rem;
    padding-right: 4.53333vw;
    position: relative;
  }
  .qdf_teacher {
    height: 1.73333rem;
    height: 17.33333vw;
    display: flex;
  }
  .qdf_teacher_item {
    display: flex;
    height: 100%;
    align-items: center;
  }
  .qdf_teacher_item img {
    width: 0.72rem;
    width: 7.2vw;
    height: 0.72rem;
    height: 7.2vw;
    border-radius: 50%;
  }
  .qdf_teacher_item font {
    font-size: 0.32rem;
    font-size: 3.2vw;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.45);
    margin-left: 0.22667rem;
    margin-left: 2.26667vw;
    margin-right: 0.46667rem;
    margin-right: 4.66667vw;
  }
  .qdf_info {
    border-top: 1px solid #f5f5f5;
    height: 1.17333rem;
    height: 11.73333vw;
    line-height: 1.17333rem;
    line-height: 11.73333vw;
    display: flex;
    font-size: 0.34667rem;
    font-size: 3.46667vw;
    font-family: PingFangSC-Regular;
  }
  .qdf_person {
    flex: 1;
    
  }
  .qdf_free {
    color: #44a426;
    font-size: 0.42667rem;
    font-size: 4.26667vw;
  }
}
.free {
  width: 100%;
  height: 130px;
  border-bottom: 1px solid #f1f1f1;
  h3 {
    font-size: 14px;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-weight: normal;
    margin-left: 30px;
  }
  ul {
    display: flex;
    // justify-content: space-around;
    flex-wrap: wrap;
    li {
      width: 70px;
      height: 30px;
      font-size: 14px;
      margin-bottom: 20px;
      border-radius: 5px;
      line-height: 30px;
      text-align: center;
      background: #f5f5f5;
      margin-left: 20px;
    }
  }
}
//排序
.sort {
  width: 100%;
  height: 300px;
  p {
    width: 100%;
    height: 60px;
    line-height: 60px;
    text-align: center;
    font-size: 16px;
  }
}
//筛选
.Filters {
  width: 100%;
  height: 130px;
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    li {
      width: 80px;
      margin-left: 10px;
      margin-top: 20px;
      border-radius: 5px;
      border: 1px solid #f1f1f1;
      line-height: 35px;
      height: 35px;
      text-align: center;
      font-size: 14px;
    }
  }
}
.bottom {
  width: 100%;
  height: 30px;
}
//分类内容样式
.ulItem {
  display: flex;
  // justify-content: space-around;
  flex-wrap: wrap;

  li {
    width: 2rem;
    font-size: 0.36rem;
    border: 1px solid #f1f1f1;
    margin: 0.2rem;
    text-align: center;
    height: 1rem;
    line-height: 1rem;
    background: #f5f5f5;
    color:#eb6100;
    border-radius:5px;
  }
}
// .slects{
//     h3{
//     font-size:12px;
//   }
// }
</style>