<template>
  <div id="qdfxqy">
    <!-- 头部 -->
   <div :class="config?'handlerClass':''">
    <van-nav-bar left-arrow @click-left="qdf_fh">
      <template #title>
        <div v-show='!config'>课程详情</div>
        <div class="menu" v-show='config'>
          <ul>
            <li @click='toStartjs'>课程介绍</li>
            <li @click='toStartbig'>课程大纲</li>
            <li @click='toComment'>课程评论</li>
          </ul>
        </div>
      </template>
      <template #right>
        <van-icon name="cluster" size="18" />
      </template>
    </van-nav-bar>
    </div>
    <!-- 头部结束 -->
      <!-- 下面内容布局1 -->
      <div class="qdf_body">
        <div class="qdf_info">
          <p class="qdf_title">{{Start.title}}</p>
          <i class="qdf_collect">
             <van-icon   v-if="show1 == 0"
          name="star"
          size="20"
           color="#ccc"
          @click="Changeclick1" 
          
        />
        <van-icon   v-if="show1 == 1"
          name="star" 
          size="20"
          @click="Changeclick2"
           color="red"
        />
          </i>
          <p class="qdf_free">{{ Start.price | priceShow}}</p>
          <div>
            <p class="info-com">共{{ Start.status }}课时 | {{ Start.sales_num }}人已报名</p>
            <p class="info-com">开课时间：{{Start.start_play_year}} - {{ Start.start_play_date | time }} ~{{ Start.start_play_year}} ~ {{ Start.end_play_date | time}}</p>
          </div>
          <!-- 下面内容布局1结束 -->
        </div>
        <!-- 内容布局2开始 -->
        <div class="qdf_teachers" v-for='(item,index) in Tea' :key='index'>
          <p class="qdf_t">教学团队</p>
          <ul>
            <li @click="qdf_tzjsxq()">
              <img :src="item.teacher_avatar" />
              <span>{{ item.teacher_name }}</span>
            </li>
          </ul>
        </div>
        <!-- 内容布局2结束-->
        <!-- 内容布局3开始 -->
        <div id="tro" class="qdf_tro" ref='startjs'>
          <p class="qdf_cd" >课程介绍</p>
          <div class="qdf_details" v-html="Start.course_details">{{ Start.course_details }}</div>
        </div>
        <!-- 内容布局3结束 -->
        <!-- 内容布局4开始 -->
        <div data-v-0b37a264 id="list" class="qdf_com-style" ref='startbig'>
          <p data-v-0b37a264 class="qdf_cd-title" >课程大纲</p>
          <div data-v-8247b44a data-v-0b37a264 class="qdf_charpter">
            <ul>
              <li data-v-8247b44a class="period">
                <div data-v-8247b44a class="charp-title">
                  <!---->
                  <span data-v-8247b44a class="huifang"></span>
                  <!---->
                 
                </div>
                <p data-v-8247b44a class="charp-time">
                 
                  <span data-v-8247b44a></span>
                </p>
              </li>
            </ul>
          </div>
          <!---->
        </div>
        <!-- 内容布局4结束 -->
        <!-- 内容布局5开始 -->
        <div data-v-0b37a264 id="com" class="qdf_cd-comment" ref='comment'>
          <p data-v-0b37a264 class="qdf_tt" >课程评论</p>
          <div data-v-ea5a5206 data-v-0b37a264 class="qdf_com-item-page">
            <div data-v-75d58a0a data-v-ea5a5206 role="feed" class="qdf_van-list">
              <ul data-v-ea5a5206></ul>
              <div class="qdf_van-list"></div>
            </div>
            <div data-v-0fa74510 data-v-ea5a5206 class="qdf_empty">
              <img data-v-0fa74510 src="../../assets/微信图片_20200705145758.png" />
              <p data-v-0fa74510>暂无评论</p>
            </div>
          </div>
        </div>
        <!-- 内容布局5结束 -->
      </div>
      <!-- 立即报名布局开始 -->
      <button  @click="qdf_tzxx()" class="course-btn van-button van-button--default van-button--normal">
        <span  class="van-button__text">立即学习</span>
      </button>
      <!-- 立即报名布局结束 -->
    </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      id:'',
      Start:[],
      Tea:[],
       show1:0,
      collect_id:"",
      config:false
    };
  },
   filters: {
    priceShow(val) {
      if (val == 0) {
        return "免费";
      } else {
        return (val / 100).toFixed(2);
      }
    },
     time(val) {
      var moment = require("moment");
      var aaa = moment(val * 1000).format("MM月DD日 h:mm:ss");
      return aaa;
    }
  },
  created() {
     document.body.scrollTop = document.documentElement.scrollTop = 0;
  },
  methods: {
    qdf_fh() {
      this.$router.go(-1);
    },
    showPopup() {
      this.show = true;
    },
    qdf_tzjsxq(){
      this.$router.push("/qdfjsxq")
    },
   qdf_tzxx(){
      this.$router.push("/qdfxx")
    },
      //滚动到课程评论
    toComment(){
      this.$refs.comment.scrollIntoView()
    },
     //滚动到课程介绍
    toStartjs(){
      this.$refs.startjs.scrollIntoView()
    },
    //滚动到课程大纲
     toStartbig(){
      this.$refs.startbig.scrollIntoView()
    },
    //收藏成功
     async Changeclick1() {
      let { data: res } = await this.$http.post("/api/app/collect", {
        course_basis_id: this.id,
        type: 1,
      });
      this.$http.get(
        `/api/app/courseInfo/basis_id=${this.id}`
      )
      .then((msg) => {
        // console.log(msg.data.data.info);
        this.show1 = msg.data.data.info.is_collect
      });
    },
    //取消收藏
     async Changeclick2(){
     console.log(this.collect_id)
      let { data: res } = await this.$http.put(`/api/app/collect/cancel/${this.collect_id}/1`);
      this.$http.get(
        `/api/app/courseInfo/basis_id=${this.id}`
      )
      .then((msg) => {
        // console.log(msg.data.data.info);
        this.show1 = msg.data.data.info.is_collect
      });
    },
    handleScrollx() {
      console.log("滚动高度", window.pageYOffset);
      this.config = window.pageYOffset > 30;
      // console.log('距离顶部高度', this.$refs.obtain.getBoundingClientRect().top)
    },
  },
  mounted() {
    //滚动条的获取
     window.addEventListener("scroll", this.handleScrollx, true);
     this.id = this.$route.query.id;
    this.$http
      .get(
        `/api/app/courseInfo/basis_id=${this.id}`
      )
      .then((msg) => {
        console.log(msg)
       
        this.Start = msg.data.data.info;
        this.Tea = msg.data.data.teachers;
        console.log(this.Start)
        console.log(this.Tea)
        this.collect_id = msg.data.data.info.collect_id
     
        this.show1 = msg.data.data.info.is_collect
      });
  }
};
</script>

