<template>
  <div class="pwd">
    <van-nav-bar
      title="找回密码"
      @blur='onBlur'
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div :style="{borderBottom:activeIndex == 1 ? '1px solid #eb6100' : '',marginRight:'30px',marginLeft:'30px',width:'80%'}" @click='activeIndex = 1'>
          <van-field v-model="tel" type="tel" @blur ='onBlur' label="手机号" placeholder="请输入手机号">
        <template #button>
          <van-button size="small" type="primary">发送验证码</van-button>
        </template>
      </van-field>
    </div>
  
    <br>
    <div :style="{borderBottom:activeIndex == 2 ? '1px solid #eb6100' : '',marginRight:'30px',marginLeft:'30px',width:'80%'}" @click='activeIndex = 2'>
       <van-field v-model="sms" center clearable label="短信验证码"  @blur ='onBlur' placeholder="请输入短信验证码"></van-field>
    </div>
   
    <br>
    <div :style="{borderBottom:activeIndex == 3 ? '1px solid #eb6100' : '',marginRight:'30px',marginLeft:'30px',width:'80%'}" @click='activeIndex = 3'>
       <van-field v-model="password" type="password"   @blur ='onBlur' label="密码" placeholder="请输入密码" />
    </div>
   
    <br>
     <button class='btn'>确定</button>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  components: {},
  props: {},
  data() {
    return {
      tel: "",
      sms: "",
      password: "",
      activeIndex:-1
    };
  },
  watch: {},
  computed: {},
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      Toast("按钮");
    },
    //失去焦点去除边框
    onBlur(){
      this.activeIndex = -1
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
.btn{
    width:350px;
    height:40px;
    color:#ffffff;
    background:orange;
    border:none;
    border-radius:20px;
    line-height:40px;
    text-align: center;
    font-size:16px;
    font-weight:normal;
    margin-left:10px;
}
</style>